import React, { Component, PropTypes } from 'react';

class AnytimeCard extends Component {
  render() {
    return (
      <div className="task-list-card">
        <div className="task-list-card__header" >
          Please drop back in and do these however often you like
        </div>
        <div className="task-list-card__content" >
          {this.props.taskListing.anytime.map(task =>
            <div className="task-list-card__item task-list-card__item--start" >
              {task.title}
            </div>
          )}
        </div>
      </div>
    );
  }
}

AnytimeCard.propTypes = {
};

export default AnytimeCard;



