import React, { Component, PropTypes } from 'react';

class SupportCard extends Component {
  render() {
    return (
      <div className="task-list-card">
        <div className="task-list-card__header" >
          Get in touch with us if you have any problems
        </div>
        <div className="task-list-card__content" >
          <div className="task-list-card__item" >
            Email our team at <a href="mailto:support@crowdlab.com" >support@crowdlab.com</a>
          </div>
          <div className="task-list-card__item" >
            Call us on 011-39-06-698-83296
          </div>
        </div>
      </div>
    );
  }
}

SupportCard.propTypes = {
};

export default SupportCard;



