import React, { Component, PropTypes } from 'react';
import ProbeCard from './ProbeCard';
import MainCard from './MainCard';
import AnytimeCard from './AnytimeCard';
import HistoryCard from './HistoryCard';
import ProfileCard from './ProfileCard';
import SupportCard from './SupportCard';

import taskListing from './taskListing.json';

class TaskCards extends Component {
  render() {
    return (
      <div className="task-list" >
        <div className="task-list__project" >
          <h3>Project</h3>
          <p>Little summary of the project</p>
        </div>
        <div className="task-list__cards" >
          <ProbeCard {...this.props} />
          <MainCard {...this.props} taskListing={taskListing} />
          <AnytimeCard {...this.props} taskListing={taskListing} />
          <HistoryCard {...this.props} taskListing={taskListing} />
          <ProfileCard {...this.props} />
          <SupportCard {...this.props} />
        </div>
      </div>
    );
  }
}

TaskCards.propTypes = {
};

export default TaskCards;



