import React, { Component, PropTypes } from 'react';

class ProbeCard extends Component {
  render() {
    if (this.props.unreadProbes == 0) {
      return <div />;
    }
    const firstMessage = this.props.probe.messages[0];
    const messages = this.props.probe.messages;
    return (
      <div className="task-list-card">
        <div className="task-list-card__header" >
          Somebody has asked you a question
        </div>
        <div className="task-list-card__content task-list-probe" >
          <div className="task-list-card__item task-list-card__item--start" onClick={this.props.setComponent} >
            <div className="task-list-probe__from" >
              {firstMessage.author} asked:
            </div>
            <div className="task-list-probe__content" >
              {messages.map(message =>
                <div className="task-list-probe-message" >
                  {message.content}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ProbeCard.propTypes = {
};

export default ProbeCard;



