import React, { Component, PropTypes } from 'react';
import TextareaAutosize from 'react-autosize-textarea';

class StartProbe extends Component {
  constructor(props) {
    super(props);
    this.onFocusBlur = this.onFocusBlur.bind(this);
    this.onChange = this.onChange.bind(this);
    this.state = { active: false, text: '' };
  }

  sendProbe() {
    this.props.addProbe(this.state.text);
    this.props.toggleProbe();
  }

  onFocusBlur() {
    this.setState({ active: !this.state.active });
  }

  onChange(event) {
    this.setState({ text: event.target.value });
  }

  getClassName(suffix = '') {
    const blockName = 'float-label';
    const baseName = blockName + suffix;
    if (this.isActiveOrNotBlank()) {
      return `${baseName} ${baseName}--active`;
    }
    return baseName;
  }

  isActiveOrNotBlank() {
    return (this.state.active) || (this.state.text !== '');
  }

  getDisplayClass() {
    const block = 'start-probe';
    if (this.props.active) {
      return `${block} ${block}--active`;
    }
    return block;
  }

  render() {
    return (
      <div className={this.getDisplayClass()} >
        <div className="start-probe__content" >
          <div className="start-probe__header" >
            <div className="start-probe-title">
              Probe Message
            </div>
            <button className="start-probe-cancel" onClick={this.props.toggleProbe} >&#10007;</button>
          </div>
          <div className="start-probe__date" >
            {new Date().toString()}
          </div>
          <div className="start-probe__response" >
            <b>Repsonse</b>
            <span>{this.props.response.task} - {this.props.response.date}</span>
          </div>
          <div className="start-probe__from" >
            <b>From</b>
            <span>Jerry Gergich</span>
          </div>
          <div className="start-probe__to" >
            <b>To</b>
            <span>{this.props.response.participant}</span>
          </div>
          <div className="start-probe__message float-label" >
            <label className={this.getClassName('__label')}>
              Message Probe Text
            </label>
            <TextareaAutosize
              className={this.getClassName('__textarea')}
              onFocus={this.onFocusBlur}
              onBlur={this.onFocusBlur}
              onChange={this.onChange}
              value={this.state.text}
              ref="replybox"
            />
          </div>
          <div className="start-probe__action" >
            <button onClick={this.sendProbe.bind(this)} >Send Probe</button>
          </div>
        </div>
      </div>
    );
  }
}

StartProbe.propTypes = {
};

export default StartProbe;



