import React, { Component, PropTypes } from 'react';
import TextareaAutosize from 'react-autosize-textarea';

class MessageMenu extends Component {
  constructor(props) {
    super(props);
    this.onFocusBlur = this.onFocusBlur.bind(this);
    this.onChange = this.onChange.bind(this);
    this.getProbes = this.getProbes.bind(this);
    this.state = { active: false, text: '' };
  }

  getProbes() {
    const { probes } = this.props;
    return probes.filter(probe =>
      probe.project.toLowerCase().indexOf(this.state.text.toLowerCase()) > -1
    );
  }

  onFocusBlur() {
    this.setState({ active: !this.state.active });
  }

  onChange(event) {
    this.setState({ text: event.target.value });
  }

  getClassName(suffix = '') {
    const blockName = 'float-label';
    const baseName = blockName + suffix;
    if (this.isActiveOrNotBlank()) {
      return `${baseName} ${baseName}--active`;
    }
    return baseName;
  }

  isActiveOrNotBlank() {
    return (this.state.active) || (this.state.text !== '');
  }

  renderFilter() {
    return (
      <div className="probe-message-reply-response__response float-label" >
        <label className={this.getClassName('__label')}>
          Find by project
        </label>
        <input
          type="text"
          className={this.getClassName('__text')}
          onFocus={this.onFocusBlur}
          onBlur={this.onFocusBlur}
          onChange={this.onChange}
          value={this.state.text}
        />
      </div>
    );
  }

  setProbe(probe) {
    this.props.setCurrentProbe(probe);
  }

  getItemClass(probe) {
    const block = 'message-menu-item';
    if (probe === this.props.probe) {
      return `${block} ${block}--active`;
    }
    return block;
  }

  render() {
    return (
      <div className="message-menu" >
        <div className="message-menu-filter" >
          {this.renderFilter()}
        </div>
      {this.getProbes().map(project =>
        <div className="message-menu-project" >
          <div className="message-menu-project__name" >
            {project.project}
          </div>
          <div className="message-menu-project__tasks" >
            {project.tasks.map(task =>
              <div className="message-menu-task" >
                <div className="message-menu-task__name" >
                  {task.task}
                </div>
                <div className="message-menu-task__probes" >
                  {task.probes.map(probe =>
                    <div className={this.getItemClass(probe)} onClick={this.setProbe.bind(this, probe)} >
                      <div className="message-menu-item__recipient" >{probe.recipient}</div>
                      <div className="message-menu-item__subject" >{probe.subject}</div>
                      <div className="message-menu-item__date" >{probe.datetime}</div>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      )}
      </div>
    );
  }
}

MessageMenu.propTypes = {
};

export default MessageMenu;



