import React, { Component, PropTypes } from 'react';

class ProbeList extends Component {
  personTest(probe) {
    return (probe.recipient === 'Jane Fonda' && probe.outstanding === 'participant');
  }

  getClassName() {
    const block = 'probe-list__summary';
    if (this.props.openProbeNav) {
      return `${block} ${block}--active`;
    }
    return block;
  }

  getProbes() {
    const probes = [];

    for (let i = 0; i < this.props.probes.length; i++) {
      const project = this.props.probes[i];
      for (let j = 0; j < project.tasks.length; j++) {
        const task = project.tasks[j];
        for (let k = 0; k < task.probes.length; k++) {
          const probe = task.probes[k];
          if (this.personTest(probe)) {
            probes.push(probe);
          }
        }
      }
    }

    return probes;
  }

  countProbes() {
    return this.getProbes().length;
  }

  render() {
    const { probes, setComponent } = this.props;

    if (this.countProbes() === 0) {
      return (<div></div>);
    }
    return (
      <div className="probe-list" >
        <div className="probe-list__heading" >
          You've been asked a question
        </div>
        <div className="probe-list__content" >
          {this.getProbes().map(probe =>
            <div className="probe-summary" onClick={setComponent.bind(this, probe)} >
              <div className="probe-summary__from" >
                <div className="probe-summary__avatar" >
                  <img className="probe-summary-avatar" src={probe.messages[0].avatar} />
                </div>
                <div className="probe-summary__researcher" >{probe.researcher} asked you a question</div>
              </div>
              <div className="probe-summary__content" >{probe.messages[0].content}</div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

ProbeList.propTypes = {
};

export default ProbeList;



