import React, { Component, PropTypes } from 'react';

class HistoryCard extends Component {
  render() {
    return (
      <div className="task-list-card">
        <div className="task-list-card__header" >
          Take a look at some of the answers you've already given
        </div>
        <div className="task-list-card__content" >
          {this.props.taskListing.history.map(task =>
            <div className="task-list-card__item task-list-card__item--view" >
              {task.title}
            </div>
          )}
        </div>
      </div>
    );
  }
}

HistoryCard.propTypes = {
};

export default HistoryCard;



