import React, { Component, PropTypes } from 'react';
import StartProbe from './StartProbe';

class ResponseGrid extends Component {
  constructor(props) {
    super(props);
    this.state = { showProbe: false, response: '' };
    this.toggleProbe = this.toggleProbe.bind(this);
  }

  toggleProbe(which) {
    let response = {
      task: 'Things about animals',
      date: 'Wednesday 26th April 2016 15:16am UTC',
      participant: 'Christine McVie'
    };
    if (which === 'beatles') {
      response = {
        task: 'Things about animals',
        date: 'Wednesday 27th April 2016 11:26am UTC',
        participant: 'Jane Fonda'
      };
    }
    this.setState({ showProbe: !this.state.showProbe, response });
  }

  render() {
    return (
      <div className="participant-profile">
        <div className="participant-profile__content" >
          <div className="participant-profile__header" >
            <h2>Things about animals</h2>
            <hr />
          </div>
          <div className="participant-profile__responses" >
            <div className="response-grid" >
              <div className="response-grid__fixcolumn" >
                <div className="response-grid__row response-grid__row--header" >
                  <div className="response-grid__cell" >
                    Participant
                  </div>
                </div>
                <div className="response-grid__row" >
                  <div className="response-grid__cell" >
                    <div className="response-grid-name" >Jane Fonda</div>
                    <div className="response-grid-date" >Wednesday 27th April 2016 11:26am UTC</div>
                    <div className="response-grid-action" >
                      <button onClick={this.toggleProbe.bind(this, 'beatles')} >
                        Probe This Response
                      </button>
                    </div>
                  </div>
                </div>
                <div className="response-grid__row" >
                  <div className="response-grid__cell" >
                    <div className="response-grid-name" >Christine McVie</div>
                    <div className="response-grid-date" >Wednesday 26th April 2016 15:16am UTC</div>
                    <div className="response-grid-action" >
                      <button onClick={this.toggleProbe.bind(this, 'fleetwood mac')} >
                        Probe This Response
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="response-grid__scrollcolumn" >
                <div className="response-grid__scrollcontent" >
                  <div className="response-grid__row response-grid__row--header" >
                    <div className="response-grid__cell" >
                      Question 1
                    </div>
                    <div className="response-grid__cell" >
                      Question 2
                    </div>
                    <div className="response-grid__cell" >
                      Question 3
                    </div>
                    <div className="response-grid__cell" >
                      Question 4
                    </div>
                  </div>
                  <div className="response-grid__row" >

                    <div className="response-grid__cell" >
                      I am the walrus
                    </div>
                    <div className="response-grid__cell" >
                      Help
                    </div>
                    <div className="response-grid__cell" >
                      Yellow Submarine
                    </div>
                    <div className="response-grid__cell" >
                      Elenor Rigby
                    </div>
                  </div>

                  <div className="response-grid__row" >
                    <div className="response-grid__cell" >
                      Monday Morning
                    </div>
                    <div className="response-grid__cell" >
                      Rhiannon
                    </div>
                    <div className="response-grid__cell" >
                      Second Hand News
                    </div>
                    <div className="response-grid__cell" >
                      The Chain
                    </div>
                  </div>
                </div>
              </div>
              {<StartProbe
                  toggleProbe={this.toggleProbe}
                  response={this.state.response}
                  {...this.props}
                  active={this.state.showProbe}
                />
              }
            </div>
          </div>
        </div>

      </div>
    );
  }
}

ResponseGrid.propTypes = {
};

export default ResponseGrid;



