import React, { Component, PropTypes } from 'react';
import Header from 'components/header/Header';
import Footer from 'components/footer/Footer';
import Inbox from 'components/Inbox';
import MessageStyle from 'components/MessageStyle';
import QuestionStyle from 'components/QuestionStyle';
import ParticipantProfile from 'components/ParticipantProfile';
import ResponseGrid from 'components/ResponseGrid';
import TaskList from 'components/TaskList';
import TaskCards from 'components/TaskCards';

import fixtures from './probes.json';

class App extends Component {
  constructor(props) {
    super(props);
    this.setCurrentProbe = this.setCurrentProbe.bind(this);
    this.state = {
      menuIsOpen: false,
      openProbeNav: false,
      probe: fixtures.probes[0].tasks[0].probes[0],
      probes: fixtures.probes,
      component: 'Inbox',
      unreadProbes: 0,
      type: 'researcher',
      expandedList: ''
    };
    this.toggleMenu = this.toggleMenu.bind(this);
    this.toggleProbeNav = this.toggleProbeNav.bind(this);
    this.hideMenu = this.hideMenu.bind(this);
    this.addMessage = this.addMessage.bind(this);
    this.pickComponent = this.pickComponent.bind(this);
  }


  setCurrentProbe(probe) {
    this.setState({ probe });
  }

  toggleMenu() {
    this.setState({ menuIsOpen: !this.state.menuIsOpen });
  }

  toggleProbeNav() {
    this.setState({ openProbeNav: !this.state.openProbeNav });
  }

  hideMenu() {
    if (this.state.menuIsOpen) {
      this.toggleMenu();
    }
    if (this.state.openProbeNav) {
      this.toggleProbeNav();
    }
  }

  addMessage(content) {
    const datetime = new Date().toString();
    const avatar = 'http://www.cbc.ca/smartestperson/content/image/avatar-placeholder.png';
    const type = this.state.type;
    const author = this.state.type === 'researcher' ? 'Jerry Gergich' : 'Jane Fonda';

    const outstanding = this.state.type === 'researcher' ? 'participant' : 'researcher';

    const message = {
      author, datetime, content, avatar, type
    };
    const probe = this.state.probe;
    probe.messages.push(message);
    probe.outstanding = outstanding;
    this.setState({ probe });
  }

  addProbe(content) {
    const datetime = new Date().toString();
    const avatar = 'http://www.cbc.ca/smartestperson/content/image/avatar-placeholder.png';
    const type = 'researcher';
    const author = 'Jerry Gergich';

    const outstanding = 'participant';

    const probe = {
      researcher: author,
      recipient: 'Jane Fonda',
      subject: content.substring(0, 20),
      datetime,
      outstanding,
      messages: [
        {
          author,
          avatar,
          datetime,
          content,
          type
        }
      ]
    };

    const probes = this.state.probes;
    probes[1].tasks[0].probes.push(probe);

    this.setState({ probe, probes });
  }

  setComponent(component) {
    let type = 'researcher';
    let unreadProbes = 0;
    switch (component) {
      case 'Inbox':
        break;
      case 'ParticipantProfile':
        break;
      case 'ParticipantProfileSide':
        break;
      case 'ResponseGrid':
        break;
      case 'TaskList':
        unreadProbes = 1;
        type = 'participant';
        break;
      case 'TaskCards':
        unreadProbes = 1;
        type = 'participant';
        break;
      case 'MessageStyle':
        type = 'participant';
        break;
      case 'QuestionStyle':
        type = 'participant';
        break;
      default:
        type = 'researcher';
    }
    this.setState({ component, type, unreadProbes });
  }

  pickComponent() {
    switch (this.state.component) {
      case 'Inbox':
        return (
          <Inbox
            probes={this.state.probes}
            probe={this.state.probe}
            addMessage={this.addMessage}
            setCurrentProbe={this.setCurrentProbe}
          />
        );
      case 'ParticipantProfile':
        return <ParticipantProfile modifier="start-probe--lightbox" addProbe={this.addProbe.bind(this)} />;
      case 'ParticipantProfileSide':
        return <ParticipantProfile modifier="start-probe--sidebar" addProbe={this.addProbe.bind(this)} />;
      case 'ResponseGrid':
        return <ResponseGrid modifier="start-probe--sidebar" addProbe={this.addProbe.bind(this)} />;
      case 'TaskList':
        return <TaskList {...this.props} probes={this.state.probes} setComponent={this.setComponent.bind(this, 'QuestionStyle')} />;
      case 'TaskCards':
        return <TaskCards {...this.props} probe={this.state.probe} setComponent={this.setComponent.bind(this, 'QuestionStyle')} />
      case 'MessageStyle':
        return (
          <MessageStyle probe={this.state.probe} addMessage={this.addMessage} />
        );
      case 'QuestionStyle':
        return (
          <QuestionStyle
            probe={this.state.probe}
            addMessage={this.addMessage}
          />
        );
      default:
        return <ParticipantProfile />;
    }
  }

  renderButton(text, componentName) {
    return (
      <button
        onClick={this.setComponent.bind(this, componentName)}
        className={this.state.component === componentName ? 'active' : 'not'}
      >
        {text}
      </button>
    );
  }

  expandList(which) {
    let expandedList = which;
    if (this.state.expandedList === which) {
      expandedList = '';
    }
    this.setState({ expandedList });
  }

  activeClassName(which) {
    const block = 'changer-role__actions';
    if (this.state.expandedList === which) {
      return `${block} ${block}--active`;
    }
    return block;
  }

  setProbeComponent(probe) {
    this.setState({ probe });
    if (this.state.type === 'participant') {
      return this.setComponent('QuestionStyle');
    }
    return this.setComponent('Inbox');
  }

  render() {
    const { participant, navigation } = fixtures;
    const { menuIsOpen, openProbeNav } = this.state;
    return (
      <div
        data-react-available="true"
        className="opinion-container"
        onClick={this.hideMenu}
      >
        <Header
          participantId={participant.id}
          navigation={navigation}
          menu={{ menuIsOpen, toggleMenu: this.toggleMenu }}
          openProbeNav={openProbeNav}
          toggleProbeNav={this.toggleProbeNav}
          unreadProbes={this.state.unreadProbes}
          probe={this.state.probe}
          probes={this.state.probes}
          type={this.state.type}
          setComponent={this.setProbeComponent.bind(this)}
        />
        <div className="opinion-content" >
          {this.pickComponent()}
        </div>
        <div className="changer" >
          <div className="changer-role" >
            <div
              className="changer-role__header"
              onClick={this.expandList.bind(this, 'jerry')}
            >
              Jerry (Dashboard)
            </div>
            <div className={this.activeClassName('jerry')} >
              <div className="changer-role-story" >
                <div className="changer-role-story__heading" >
                  3.2 Jerry must be able to send a probe to Jane about a response
                </div>
                {/*<div className="changer-role-story__actions" >
                  {this.renderButton("From Participant's Profile - Lightbox", 'ParticipantProfile')}
                </div>*/}
                <div className="changer-role-story__actions" >
                  {this.renderButton("From Participant's Profile - Sidebar", 'ParticipantProfileSide')}
                </div>
                <div className="changer-role-story__actions" >
                  {this.renderButton("From Response Grid - Sidebar", 'ResponseGrid')}
                </div>
              </div>
              <div className="changer-role-story" >
                <div className="changer-role-story__heading" >
                  3.4 Jerry must read the probe reply
                </div>
                <div className="changer-role-story__actions" >
                  {this.renderButton('Inbox', 'Inbox')}
                </div>
              </div>
            </div>
          </div>
          <div className="changer__role" >
            <div
              className="changer-role__header"
              onClick={this.expandList.bind(this, 'jane')}
            >
              Jane (Discussion)
            </div>
            <div className={this.activeClassName('jane')} >
              <div className="changer-role-story" >
                <div className="changer-role-story__heading" >
                  5.6 Jane must know that he/she has been probed
                </div>
                <div className="changer-role-story__actions" >
                  {this.renderButton('Task List', 'TaskList')}
                  {this.renderButton('Task List Card View', 'TaskCards')}
                </div>
              </div>
              <div className="changer-role-story" >
                <div className="changer-role-story__heading" >
                  5.4 Jane responds to Jerry's probe
                </div>
                <div className="changer-role-story__actions" >
                  {this.renderButton('Message Style Probe', 'MessageStyle')}
                  {this.renderButton('Question Style Probe', 'QuestionStyle')}
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer navigation={navigation} />
      </div>
    );
  }
}

App.propTypes = {
  participant: PropTypes.shape({
    id: PropTypes.number.isRequired
  }).isRequired,
  navigation: PropTypes.shape({
    taskList: PropTypes.shape({
      path: PropTypes.string.isRequired,
      projectName: PropTypes.string.isRequired,
      taskName: PropTypes.string.isRequired
    }),
    metadata: PropTypes.shape({
      support_email: PropTypes.string.isRequired,
      website_link_text: PropTypes.string.isRequired,
      website_url: PropTypes.string,
      copyright_agencies: PropTypes.string.isRequired,
      copyright_period: PropTypes.string.isRequired
    })
  })
};

export default App;



